import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { NinjutsuService } from '../ninjutsu.service';

@Component({
  selector: 'app-ninjutsu',
  templateUrl: './ninjutsu.page.html',
  styleUrls: ['./ninjutsu.page.scss'],
})
export class NinjutsuPage implements OnInit {

  id: number;
  ninjutsuData: Object;

  constructor(
    public route: ActivatedRoute,
    public ninjutsu: NinjutsuService
  ) { 
    this.id = parseInt( this.route.snapshot.paramMap.get("id") );
    this.ninjutsuData = ninjutsu.getOneNinjutsu(this.id);
  }

  ngOnInit() {
  }

}
